<template>
    <div class="page-wrapper">

        <Sidebar/>
        <div class="main-container">

            <Navbar page="Painel de Controle"/>
            <div class="content-wrapper-scroll">

                <div class="content-wrapper">

                    <div class=" d-flex justify-content-between mb-2">
                        <h4 class = "fw-bold">Bem vindo (a) {{user.nome}}</h4>
                        <h4 class = "fw-bold text-end">{{user.nivel}}</h4>
                    </div>

                    <div class="row">
                        <div class="col-xxl-3 col-md-3 col-12">
                            <div class="stats-tile">
                                <div class="sale-icon shade-blue">
                                    <i class="fas fa-user-plus"></i>
                                </div>
                                <div class="sale-details">
                                    <h3 class="text-blue">{{registros.inscricoes}}</h3>
                                    <p>Estudantes</p>
                                </div>
                            </div>
                        </div>
                        <div class="col-xxl-3 col-md-3 col-12">
                            <div class="stats-tile">
                                <div class="sale-icon shade-secondary">
                                    <i class="fas fa-user-pen"></i>
                                </div>
                                <div class="sale-details">
                                    <h3 class="text-secondary">{{registros.matriculas}}</h3>
                                    <p>Matriculas</p>
                                </div>
                            </div>
                        </div>
                        <div class="col-xxl-3 col-md-3 col-12">
                            <div class="stats-tile">
                                <div class="sale-icon shade-green">
                                    <i class="fas fa-users"></i>
                                </div>
                                <div class="sale-details">
                                    <h3 class="text-green">{{registros.matriculados}}</h3>
                                    <p>Matriculados</p>
                                </div>
                            </div>
                        </div>
                        <div class="col-xxl-3 col-md-3 col-12">
                            <div class="stats-tile">
                                <div class="sale-icon shade-red">
                                    <i class="fas fa-users-slash"></i>
                                </div>
                                <div class="sale-details">
                                    <h3 class="text-red">{{registros.desistentes}}</h3>
                                    <p>Desistentes</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row" v-if = "user.nivel === 'Administrador' || user.nivel === 'IT'">
                        <router-link to="/admin/finances/fluxo-de-caixa" class="col-md-3 text-decoration-none">
                            <div class="card mw-100 mh-auto text-white bg-primary" style="height: auto;">
                                <div class="card-header">
                                    <h4>
                                        <i class="fas fa-calendar-day"></i> Saldo Dirio
                                    </h4>
                                </div>
                                <div class="card-body">
                                    <h3>{{ numberFormat(saldo.daily, 2, ',', '.') }} Kz</h3>
                                </div>
                            </div>
                        </router-link>

                        <router-link to="/admin/finances/fluxo-de-caixa" class="col-md-3 text-decoration-none">
                            <div class="card text-white bg-success">
                                <div class="card-header mw-100 text-truncate">
                                    <h4>
                                        <i class="fas fa-calendar-week"></i> Saldo Semanal
                                    </h4>
                                </div>
                                <div class="card-body">
                                    <h3>{{ numberFormat(saldo.weekly, 2, ',', '.') }} Kz</h3>
                                </div>
                            </div>
                        </router-link>

                        <router-link to="/admin/finances/fluxo-de-caixa" class="col-md-3 text-decoration-none">
                            <div class="card text-white bg-warning">
                                <div class="card-header">
                                    <h4>
                                        <i class="fas fa-calendar-alt"></i> Saldo Mensal
                                    </h4>
                                </div>
                                <div class="card-body">
                                    <h3>{{ numberFormat(saldo.monthly, 2, ',', '.') }} Kz</h3>
                                </div>
                            </div>
                        </router-link>

                        <router-link to="/admin/finances/fluxo-de-caixa" class="col-md-3 text-decoration-none">
                            <div class="card text-white bg-danger">
                                <div class="card-header">
                                    <h4>
                                        <i class="fas fa-chart-bar"></i> Saldo Anual
                                    </h4>
                                </div>
                                <div class="card-body">
                                    <h3>{{ numberFormat(saldo.yearly, 2, ',', '.') }} Kz</h3>
                                </div>
                            </div>
                        </router-link>
                    </div>
                    <div class="row">
                        
                        <div class="col-xxl-6 col-sm-6 col-md-6">
                            <div class="card" style = "height:18.5em">
                                <div class="card-header">
                                    <h4 class="card-title">Notificaes Recentes</h4>
                                    <router-link to="/admin/auth/security-logs" class="btn btn-sm btn-outline-primary">Ver Todas</router-link>
                                </div>
                                <div class="card-body pt-2" style = "overflow-x:hidden; overflow-y:auto">
                                    
                                    <div v-if="notifications.length === 0" class="text-center my-5">
                                        <p class="text-secondary">Nenhuma notificao recente.</p>
                                    </div>
                                    
                                    <div v-else 
                                        class="activity" 
                                        v-for="notif in notifications" 
                                        :key="notif.id"
                                    >
                                        <div class="activity-avatar bg-primary">
                                            <i class="fas fa-bell"></i>
                                        </div>
                                        <div>
                                            <p class="activity-title fw-bold text-truncate mb-0">{{ notif.title }}</p>
                                            <p class="activity-text text-secondary mb-0">{{ notif.text }}</p> 
                                            <small class="text-muted">{{ moment(notif.createdAt).fromNow() }}</small>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                        
                        <div class="col-xxl-6 col-sm-6 col-md-6">
                            <div class="card" style = "height:18.5em">
                                <div class="card-header">
                                    <h4 class="card-title">Usurios Conectados</h4>
                                    <div>
                                        <span class = "fas fa-users"></span>
                                        <b>
                                        ( {{users.length}} )
                                        </b>
                                    </div>
                                </div>
                                <div class="card-body pt-0" style = "overflow-x:hidden; overflow-y:auto">
                                    <div class="table-responsive">
                                        <table class="table v-middle">
                                            <thead>
                                                <tr class="bg-secondary">
                                                    <th>Nome</th>
                                                    <th>Status</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr 
                                                    v-for="user in users" 
                                                    :key="user.id"
                                                >
                                                    <td>
                                                        <div class="media-box">
                                                            <img :src="`${url}/api/mediafiles/`+user.foto" class="media-avatar" alt="Admin Themes" style = "width:3em; height:3em; border-radius:100px !important">
                                                            <div class="media-box-body">
                                                                <div class="text-truncate">{{user.nome}}</div>
                                                            </div>
                                                        </div>
                                                    </td>
                                                    <td>
                                                        <span v-if="user.status" class="text-green td-status"><i class="bi bi-check-circle"></i> Online</span>
                                                        <span v-else class="text-danger td-status"><i class="bi bi-x-circle"></i> Offline</span>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-xxl-12 col-sm-12 col-md-12" v-if = "user.nivel === 'Administrador' || user.nivel === 'IT'">
                            <div class="card" style="height:auto">
                                <div class="card-header">
                                    <h4 class="card-title">Grfico de Pagamentos Referentes Aos Meses de {{new Date().getFullYear()}}</h4>
                                </div>
                                <div class="card-body">
                                    <Bar
                                        id="monthly-payments-chart"
                                        :options="monthlyChartOptions"
                                        :data="monthlyChartData"
                                    />
                                </div>
                            </div>
                        </div>

                        <div class="col-xxl-12 col-sm-12 col-md-12" v-if="user.nivel === 'Administrador' || user.nivel === 'IT'">
                            <div class="card" style="height:auto">
                                <div class="card-header">
                                    <h4 class="card-title">Total de pagamentos por Ano</h4>
                                </div>
                                <div class="card-body">
                                    <Bar
                                        id="annual-payments-chart"
                                        :options="annualChartOptions"
                                        :data="annualChartData"
                                    />
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
                <Footer/>
                </div>
            </div>
        <Loader v-if="isLoading"/>
    </div>
    </template>

<script>
    import './static';
    import Sidebar from './components/Sidebar.vue';
    import Navbar from './components/Navbar.vue';
    import Footer from './components/Footer.vue';
    import Loader from './components/Loader.vue';
    import { Bar } from 'vue-chartjs';
    import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, ArcElement, CategoryScale, LinearScale } from 'chart.js';
    import { api, url, socket, numberFormat } from '../../helpers/api';
    import { mapState } from 'vuex';
    import moment from 'moment';

    ChartJS.register(Title, Tooltip, Legend, BarElement, ArcElement, CategoryScale, LinearScale);

    export default {
        name: "dashboard_view",
        components: { Sidebar, Navbar, Footer, Loader, Bar },
        data() {
            return {
                users: [],
                notifications: [], // Inicializado para as notificaes recentes
                is_student: false,
                isLoading:true,
                url: url,
                user:{},
                year: {},
                saldo:{
                    daily:0,
                    weekly:0,
                    monthly:0,
                    yearly:0
                },
                // ... (O resto do data, como chartData, aproveitamentoData, etc., mantido)
                chartData: {
                    labels: ['T.Estudantes', 'T.Matriculados'],
                    datasets: [{
                        label: 'Total de Registos',
                        backgroundColor: '#f87979',
                        data: [0, 0]
                    }]
                },
                aproveitamentoData: {
                    labels: ['Aprovados', 'Reprovados'],
                    datasets: [{
                        label: 'Aproveitamento',
                        backgroundColor: ['green', 'red'],
                        data: [60, 40]
                    }]
                },
                estatisticaData: {
                    labels: ['Masculino', 'Feminino'],
                    datasets: [{
                        label: 'Estatstica por Gnero',
                        backgroundColor: ['black', 'pink'],
                        data: [30, 20]
                    }]
                },
                chartOptions: {
                    responsive: true
                },
                registros: {
                    inscricoes: 0,
                    matriculas: 0,
                    matriculados: 0,
                    desistentes: 0,
                    aprovados: 0,
                    reprovados: 0,
                },
                monthlyChartData: {
                    labels: [
                        "Setembro", "Outubro", "Novembro", "Dezembro",
                        "Janeiro", "Fevereiro", "Maro", "Abril",
                        "Maio", "Junho", "Julho"
                    ],
                    datasets: [
                        {
                            label: "Pagamentos Mensais",
                            backgroundColor: "#42A5F5",
                            data: Array(11).fill(0),
                        }
                    ],
                },
                monthlyChartOptions: {
                    responsive: true,
                    plugins: {
                        legend: { position: "top" },
                    },
                    scales: {
                        y: { beginAtZero: true },
                    },
                },
                annualChartData: {
                    labels: ["2021", "2022", "2023", "2024", "2025", "2026"],
                    datasets: [
                        {
                            label: "Pagamentos Anuais",
                            backgroundColor: "#66BB6A",
                            data: Array(5).fill(0),
                        },
                    ],
                },
                annualChartOptions: {
                    responsive: true,
                    plugins: {
                        legend: { position: "top" },
                    },
                    scales: {
                        y: { beginAtZero: true },
                    },
                },
            }
        },
        mounted() {
            this.countData();
            this.getUsers();
            this.getYear();
            this.getRecentNotifications(); // <<< Chamada da nova funo
            this.checkLicence();
            socket.on("userChange", () => {
                this.getUsers()
            })
            socket.on("paymentChange", () => {
                this.countData()
            })
            this.user = this.$store.state.user;
        },
        computed: {
            ...mapState(['user'])
        },
        methods: {
            numberFormat,
            async countData() {
                try {
                    // ... (Todas as chamadas existentes para matriculas, inscricoes, saldos, e grficos)
                    this.get_matriculas = await api.get('/matricula/');
                    this.registros.matriculas = this.get_matriculas.data.length;

                    this.get_inscricoes = await api.get('/estudantes/');
                    this.registros.inscricoes = this.get_inscricoes.data.length;

                    this.get_matriculados = await api.get('/matricula/estado/Estudando');
                    this.registros.matriculados = this.get_matriculados.data.length;

                    this.get_desistentes = await api.get('/matricula/estado/Cancelada');
                    this.registros.desistentes = this.get_desistentes.data.length;

                    const get_balance = await api.get(`/pagamentos/balancos/getAll/${this.year.id}`)
                    this.saldo = get_balance.data;

                    this.chartData = {
                        labels: ['T.Estudantes', 'T.Matriculados'],
                        datasets: [{
                            label: 'Total de Registos',
                            backgroundColor: '#f87979',
                            data: [
                                this.registros.inscricoes,
                                this.registros.matriculados
                            ]
                        }]
                    };
                    
                    // Pagamentos Mensais
                    const response = await api.get("/pagamentos/list/mensais");
                    const payments = response.data;
                    const meses = [
                        "Janeiro", "Fevereiro", "Maro", "Abril", "Maio", "Junho",
                        "Julho", "Agosto", "Setembro", "Outubro", "Novembro", "Dezembro"
                    ];
                    const pagamentosMensais = Array(12).fill(0);

                    payments.forEach(({ mes, totalPagamentos }) => {
                        pagamentosMensais[mes - 1] = totalPagamentos;
                    });

                    this.monthlyChartData = {
                        labels: meses,
                        datasets: [
                            {
                                label: "Pagamentos Mensais",
                                backgroundColor: "#42A5F5",
                                data: pagamentosMensais,
                            },
                        ],
                    };

                    // Pagamentos Anuais
                    const annualResponse = await api.get("/pagamentos/list/anuais");
                    const annualPayments = annualResponse.data;
                    const anos = ["2021", "2022", "2023", "2024", "2025", "2026"];
                    const pagamentosAnuais = Array(6).fill(0);

                    annualPayments.forEach(({ ano, totalPagamentos }) => {
                        const index = anos.indexOf(ano.toString());
                        if (index !== -1) {
                            pagamentosAnuais[index] = totalPagamentos;
                        }
                    });

                    this.annualChartData = {
                        labels: anos,
                        datasets: [
                            {
                                label: "Pagamentos Anuais",
                                backgroundColor: "#66BB6A",
                                data: pagamentosAnuais,
                            },
                        ],
                    };

                    // NOTA: O this.isLoading = false foi movido para o getRecentNotifications, 
                    // pois geralmente  o ltimo dado a ser carregado no dashboard.

                } catch (err) {
                    console.error(err);
                    this.$swal.fire({
                        title: "Erro",
                        text: "Verifique a sua conexo ou a API",
                        icon: "error",
                        timer: 3000
                    });
                    this.isLoading = false;
                }
            },
            
            // <<< NOVO MTODO PARA BUSCAR NOTIFICAES RECENTES >>>
            async getRecentNotifications() {
                try {
                    // Nota: O isLoading j deve estar true, mas garantimos aqui
                    // this.isLoading = true; 
                    const response = await api.get("/notifications/recent");
                    this.notifications = response.data;
                } catch (err) {
                    console.error("Erro ao buscar notificaes recentes:", err);
                    // Pode adicionar um alerta de erro aqui, se preferir.
                } finally {
                    // Garante que o loader desaparea aps todas as chamadas principais.
                    this.isLoading = false;
                }
            },

            checkLicence() {
                api
                    .get("/license/read/")
                    .then(res => {
                        console.log(res.data);
                    })
                    .catch(err => {
                        if (err.response.data.message === "Licena no encontrada") {
                            this.$router.push("/licence");
                        }
                    });
            },
            getUsers() {
                api
                    .get('/utilizadores/status/1/')
                    .then(res => {
                        this.users = res.data;
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            moment,
            getYear() {
                api
                    .get(`/anos/status/1`)
                    .then(res => {
                        this.year = res.data[0];
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }
        },
    };
</script>

<style>
/* Estilos para o componente 'activity' (Notificaes) */
.activity {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 15px; /* Aumentado para melhor espaamento */
  align-items: center; /* Centraliza verticalmente o cone e o texto */
}

.activity-avatar {
  width: 3em; /* Levemente reduzido */
  height: 3em;
  background: #42A5F5; /* Usando cor primria para consistncia */
  color: white;
  text-align: center;
  margin-right: 12px;
  border-radius: 100px;
  /* Ajustado o padding-top para centralizar o cone */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
}

.activity-title {
  margin-bottom: 2px !important;
}

.activity-text {
  margin-top: 0;
  font-size: 12px;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 90%; /* Limita a largura do texto para a truncagem funcionar */
}
</style>